export class Language {
    code: string;
    alternateCode: string;
    name: string;

    constructor(code: string, alternateCode: string, name: string) {
        this.code = code;
        this.alternateCode = alternateCode;
        this.name = name; 
    }

    is(code) {
        return this.code === code || this.alternateCode === code;
    }
}

